import { PlusIcon, SendIcon } from "lucide-react";
import { Button } from "../ui/button";
import { Card, CardContent, CardHeader } from "../ui/card";
import { Input } from "../ui/input";
import { useRef } from "react";
import { axiosInstance } from "@/app/axiosInstance";
import { toast, Toaster } from "sonner";

export default function ChatboxLanding() {
    const prompt = useRef<HTMLInputElement | null>(null)

    async function handlePrompt(userPrompt : string){
        try {
            const response = await axiosInstance.post("/template" , {
                userPrompt
            })

            console.log(response.data);
        }
        catch (err) {
            console.log(err);
        }
    }


    return (
        <div className="w-130 h-45">
            <Toaster/>
            <Card>
                <CardHeader className="text-[15px] font-semibold">Create Any Website by Just Prompting..</CardHeader>
                <CardContent>
                    <div className="flex gap-3">
                    <Input ref={prompt} className="h-19" placeholder="What you are creating today Just paste your thoughts." onClick={async()=>{
                            if(prompt.current != null && prompt.current.value != "") {
                                await handlePrompt(prompt.current.value);
                            }
                            else {
                                toast.error("Write Correct Prompt")
                            }
                    }}/>
                    
                    <div className="flex flex-col gap-1 justify-end">
                        <Button ><PlusIcon/></Button>
                        <Button ><SendIcon/></Button>
                    </div>

                    </div>
                </CardContent>
            </Card>
        </div>
    )
}